import { isFunction } from "./base";

/**
 * 
 * 
 * 
 * const scheduler = new TaskScheduler(interval)
 * scheduler.add(task1)
 * scheduler.add(task2)
 */
export default class TaskScheduler {
  // 
  static get default() {
    return new TaskScheduler();
  }

  constructor(interval = 20) {
    this._interval = interval;
    this._isRunning = false;
    this._tasks = [];
    this._timerId = null;
  }

  add(task) {
    if (!isFunction(task)) return;

    this._tasks.push(task);
  }

  addAndStart(task) {
    this.add(task);
    this.start({ immediate: false });
  }

  /**
   * 
   * @param {Object} params
   * @param {Boolean} params.immediate 
   */
  start({ immediate = true } = {}) {
    if (this._isRunning) return;

    if (immediate && this._tasks.length > 0) {
      const task = this._tasks.shift();
      task.call(null);
    }

    this._runTask();
  }

  // 
  stop() {
    if (this._timerId !== null) {
      clearTimeout(this._timerId);
      this._timerId = null;
    }
    this._tasks = [];
    this._isRunning = false;
  }

  _runTask() {
    if (this._tasks.length > 0 && !this._isRunning) {
      this._isRunning = true;
      const task = this._tasks.shift();
      this._timerId = setTimeout(() => {
        task.call(null);
        this._isRunning = false;
        this._runTask();
      }, this._interval);
    }
  }
}
